/*Formalities*/
* {
    margin: 0;
    padding: 0;
    max-width: 1440px;
  }

/*Service and More Cards styling*/
.serviceCard {
    height: 510px;
    width: 370px;
    background-color: #BCEBFC;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    transition: 0.5s ease-in-out;
}

.serviceCard:hover{
    color: white;
    background-color: #3FBFE9;
    height: 520px;
    width: 380px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.logo {
    height: 145px;
    width: 145px;
    margin-top: 60px;
}

.contactButton {
    width: 170px;
    height: 50px;
    border-radius: 26px;
}

.aboutLogo {
    margin: 20px;
    align-items: center;
    display: flex;
    flex-direction: column;
    text-align: center;
}

.cardName {
    font-size: 26px;
    font-style: bold;
}

.cardDescription{
    padding: 10px;
    font-size: 20px;
}

.contactButton {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 30px;
}

.moreDetails{
    font-size: 70px;
    font-weight: 700;
    color: white;
    padding: 55px;
    text-align: center;
}

/*Service Card section*/
.serviceCardSection{
    display: grid;
    grid-template-columns: auto auto auto;
    margin-right: 141px;
    margin-left: 142px;
    grid-row-gap: 70px;
    row-gap: 70px;
    grid-column-gap: 28px;
    -webkit-column-gap: 28px;
            column-gap: 28px;

}

/*Service heading*/
.ourServices{
    font-size: 72px;
    text-align: center;
    font-weight: 700;
}

/*Domain Availability checker section*/
.domainChecker{
    width: 1162px;
    height: 400px;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-shadow: 0px 6px 16px rgba(192, 192, 192, 0.85);
    border-radius: 20px;
    align-self: center;
    margin-bottom: 100px;
    margin-top: 100px;
    justify-content: space-around;
}

.domainCheckerHeading{
    display: flex;
    align-items: center;
    justify-content: center;
}

.wwwImage{
    height: 95px;
    width: 100px;
}

.domCheckHeading{
    padding-left: 10px;
    font-family: 'Bahnschrift';
    font-style: normal;
    font-weight: 700;
    font-size: 52px;
    line-height: 72px;
}

.domainCheckerBody{
 display: flex;
 flex-direction: row;
 align-items: center;
 margin-bottom: 60px;
}

.domainField{
    height: 70px;
    width: 700px;
    border-radius: 10px;
    font-size: 30px;
    font-weight: 700;
    margin-right: 50px;
}

.domainCheckButton{
    height: 76px;
    background-color: #3FBFE9;
    width: 150px;
    border-radius: 20px;
    font-size: 30px;
    font-weight: 700;
}

.domainCheckButton:hover{
    opacity: 0.8;
}

.domainCheckButton:active {
    color: #BCEBFC;
    -webkit-filter: 0.6;
            filter: 0.6;
}

/*Service page*/
.ServicesPage{
    display: flex;
    flex-direction: column;
}

